<template>
  <div class="Login">
    <div style="margin: 20px">
      <h2>用户登录</h2>
    </div>
    <Form ref="formInline" :model="formInline" :rules="ruleInline">
      <FormItem prop="username">
        <Input type="text" v-model="formInline.username" autofocus placeholder="工号">
        <Icon type="ios-contact-outline" slot="prepend"></Icon>
        </Input>
      </FormItem>
      <FormItem prop="password">
        <Input type="password" v-model="formInline.password" placeholder="密码">
        <Icon type="ios-lock-outline" slot="prepend"></Icon>
        </Input>
      </FormItem>
      <FormItem>
        <Button type="primary" @click="handleSubmit('formInline')">登录</Button>
        <router-link to="/recharge">
          <Button type="primary" style="margin-left: 20px">充值</Button>
        </router-link>
      </FormItem>
    </Form>
    <p style="text-align: left">
      输入考核云平台账号信息即可，如忘记密码请去App上进行重置。
    </p>
  </div>
</template>
<script>
export default {
  data() {
    return {
      formInline: {
        username: "",
        password: "",
      },
      isVip: false,
      ruleInline: {
        username: [
          {
            required: true,
            message: "请输入工号",
            trigger: "blur",
          },
        ],
        password: [
          {
            required: true,
            message: "请输入密码",
            trigger: "blur",
          },
          {
            type: "string",
            min: 6,
            message: "密码不能少于6位",
            trigger: "blur",
          },
        ],
      },
    };
  },
  methods: {
    checkVip(users) {
      // console.log(users);
      let app_id = 2;
      let secret_key = "DIy5BmbglwD4uQxwjYaplaBJLO5tQmYL";
      let username = 'kaoheyun' + users.username;
      let password = 'kaoheyun' + users.userid;
      this.$axios.get("http://111.229.103.199/app_user_api/userregister", {
        params: {
          app_id: app_id,
          secret_key: secret_key,
          username: username,
          password: password,
          enpassword: password,
        }
      }).then(() => {
        this.$axios.get("http://111.229.103.199/app_user_api/getusertoken", {
          params: {
            app_id: app_id,
            secret_key: secret_key,
            username: username,
            password: password,
          }
        }).then((res) => {
          if (res.data.code) {
            let token = res.data.data.token;
            this.$axios.get("http://111.229.103.199/app_user_api/getuserapp", {
              params: {
                app_id: app_id,
                token: token,
                device_code: username,
              }
            }).then((res) => {
              if(res.data.code) {
                // console.log(res.data);
                if(res.data.data.points) this.isVip = true;
                this.$router.push({
                  name: "home",
                  params: {
                    users: users,
                    isVip: this.isVip,
                    points: res.data.data.points,
                  },
                });
              }else{
                this.$Message.error(res.data.content);
              }
            });
          } else {
            this.$Message.error(res.data.content);
          }
        });
      });
    },
    handleSubmit(name) {
      this.$Spin.show();
      this.$refs[name].validate((valid) => {
        if (valid) {
          let Url =
            "http://yun.hbgs.online:8081/kaoshi/index.php?user-phone-logmui";
          let data = {
            args: {
              username: this.formInline.username,
              userpassword: this.formInline.password,
            },
            userlogin: "1",
            userhash: "0.640034068151828",
          };
          data = this.$qs.stringify(data);
          this.$axios.post(Url, data).then((rsp) => {
            let data = rsp.data;
            if (data.statusCode === 200) {
              setTimeout(() => {
                this.$Spin.hide();
                this.$Message.success("登录成功！");
                // console.log(data.users);
                this.checkVip(data.users);
              }, 1000);
            } else {
              setTimeout(() => {
                this.$Spin.hide();
                this.$Message.error(data.message);
              }, 1000);
            }
          });
        } else {
          setTimeout(() => {
            this.$Spin.hide();
            this.$Message.error("请检查工号密码是否输入正确！");
          }, 1000);
        }
      });
    },
  },
};
</script>
<style>
.Login {
  width: 80vw;
  max-width: 420px;
  margin: 0 auto;
  min-height: 300px;
  height: 46vh;
  margin-top: 10vh;
}
</style>
